.preloader{
    position: fixed;
    background: #121212;
    height: 100vh;
    width: 100%;
    margin: 0 auto;
    z-index: 100;
    transition: 1s all;
    opacity: 1;
    visibility: visible;
    &.none{
      visibility: hidden;
      display: none;
      opacity: 0;
  }
}
.elips1{
    background-color: #fff;
    border: 3px solid #fff;
    width: 35px; height: 35px;
    border-radius: 100%;
    position: absolute;
    left: 50%; top: 50%;
    opacity:0;
  }
