.home {
    background: #121212;

    &__inner {
        display: flex;
        width: calc(100% - 130px);
        height: 100vh;
        align-items: center;
        margin-left: 130px;
    }

    &__block {
        display: flex;
        align-items: center;
        width: 300px;
        height: 100vh;
        margin-left: auto;
        background-color: #121212;
        box-shadow: 0 0 10px #000;
        position: relative;
    }

    &__block-text {
        position: absolute;
        width: 550px;
        font-size: 60px;
        line-height: 60px;
        font-weight: 500;
        text-transform: uppercase;
        letter-spacing: 1px;
        color: #191919;
        transform: rotate(-90deg);
        right: -40%;
        user-select: none;
        animation: animate-text 5s linear infinite;
    }

    @keyframes animate-text {
        0%,18%,20%,50.1%,60%,65%,80%,90.1%,92%{
            color:#191919;
            text-shadow: none;
        }
        100%,18.1%,20.1%,50%,60.1%,65.1%,80.1%,90%,92.1%{
            color:#fff;
            text-shadow: 0 0 10px #fff,
            0 0 20px #fff,
            0 0 40px #fff,
            0 0 80px #fff;
        }
    }

    &__title {
        font-size: 140px;
        font-weight: 700;
        line-height: 140px;
        color: #fff;
        letter-spacing: 2px;
    }

    &__sub-title {
        position: relative;
        color: #222;

        &::before {
            content: attr(data-text);
            position: absolute;
            color: #fff;
            width: 100px;
            height: 160px;
            overflow: hidden;
            border-right: 2px solid #fff;
            animation: typing 8s linear infinite;
        }
    }

    @keyframes typing {

        0%,
        10%,
        100% {
            width: 0;
        }

        70%,
        90% {
            width: 100%;
        }
    }
}

.glitch {
    position: relative;
    color: white;
    mix-blend-mode: lighten;

    &:before,
    &:after {
        content: attr(data-text);
        position: absolute;
        top: 0;
        width: 100%;
        background: black;
        clip: rect(0, 0, 0, 0);
    }

    &:before {
        left: -1px;
        text-shadow: 1px 0 rgba(red, 0.7);
    }

    &:after {
        left: 1px;
        text-shadow: -1px 0 rgba(blue, 0.7);
    }

    &:hover {
        &:before {
            text-shadow: 4px 0 rgba(red, 0.7);
            animation: glitch-loop-1 0.8s infinite ease-in-out alternate-reverse;
        }

        &:after {
            text-shadow: -5px 0 rgba(blue, 0.7);
            animation: glitch-loop-2 0.8s infinite ease-in-out alternate-reverse;
        }
    }
}

@keyframes glitch-loop-1 {
    0% {
        clip: rect(36px, 9999px, 9px, 0)
    }

    25% {
        clip: rect(25px, 9999px, 99px, 0)
    }

    50% {
        clip: rect(50px, 9999px, 102px, 0)
    }

    75% {
        clip: rect(30px, 9999px, 92px, 0)
    }

    100% {
        clip: rect(91px, 9999px, 98px, 0)
    }
}

@keyframes glitch-loop-2 {
    0% {
        top: -1px;
        left: 1px;
        clip: rect(65px, 9999px, 119px, 0)
    }

    25% {
        top: -6px;
        left: 4px;
        clip: rect(79px, 9999px, 19px, 0)
    }

    50% {
        top: -3px;
        left: 2px;
        clip: rect(68px, 9999px, 11px, 0)
    }

    75% {
        top: 0px;
        left: -4px;
        clip: rect(95px, 9999px, 53px, 0)
    }

    100% {
        top: -1px;
        left: -1px;
        clip: rect(31px, 9999px, 149px, 0)
    }
}

.wrapper {
    height: 100vh;
    overflow: auto;
    scroll-snap-type: y mandatory;
}

.section {
    height: 100vh;
    scroll-snap-align: start;
}

@media (max-width: 1500px) {
    .home {
        &__block {
            margin-right: 20px;
        }
    }
}

@media (max-width: 1100px) {
    .home {
        &__inner {
            padding-top: 80px;
            height: calc(100vh - 80px);
        }

        &__block {
            margin-top: -80px;
        }

    }
}
@media (max-width: 1099px) {   
    .home{
        &__inner{
            flex-direction: column;
            align-items: start;
            padding-top: 200px;
            margin: 0;
        }
        &__title{
            width: 100%;
            font-size: 52px;
            line-height: 52px;
            text-align: center;
            margin-bottom: 200px;
            text-align: center;
        }
        &__block {
            display: flex;
            width: 100vw;
            height: 100px;
            margin: 0;
        }
    
        &__block-text {
            position: absolute;
            width: 100%;
            font-size: 40px;
            line-height: 40px;
            transform: rotate(0deg);
            right: 0; left: 0;
            margin: 0 auto;
            text-align: center;
        }
        &__sub-title {
            &::before {
                width: 100px;
                height: 60px;
            }
        }
    }
}