.header {
    &__inner {
        display: block;
        position: fixed;
        top: 0;
        left: 0;
        width: 100px;
        height: 100vh;
    }

    &__line {
        position: absolute;
        width: 1px;
        height: 100%;
        top: 0;
        right: 0;
        background: rgba(255, 255, 255, 0.1);
        overflow: hidden;

        &::after {
            content: '';
            display: block;
            position: absolute;
            height: 15vh;
            width: 100%;
            top: -50%;
            left: 0;
            background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, #ffffff 75%, #ffffff 100%);
            animation: drop 7s 0s infinite;
            animation-fill-mode: forwards;
            animation-timing-function: cubic-bezier(0.4, 0.26, 0, 0.97);
        }
    }

    @keyframes drop {
        0% {
            top: -50%
        }

        100% {
            top: 110%
        }
    }

    &__menu-btn {
        display: block;
        border: none;
        outline: none;
        background-color: transparent;
        width: 50px;
        height: 50px;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 30px;
        margin: auto 0;
        cursor: pointer;
        z-index: 100;

        &:hover {
            .header__menu-btn--lines {
                animation: menubtn 1s linear;

                &::before {
                    animation-delay: 0.3s;
                    animation: menubtn1 1s linear;
                }

                &::after {
                    animation-delay: 0.4s;
                    animation: menubtn2 1s linear;
                }
            }
        }
    }

    &__close-btn {
        display: block;
        border: none;
        outline: none;
        background-color: transparent;
        width: 50px;
        height: 50px;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 30px;
        margin: auto 0;
        cursor: pointer;
        z-index: 100;

        &:hover {
            .header__close-btn--lines {
                transition: all 0.3s;
                &::before {
                    animation: rotate_close 1s linear;
                }

                &::after {
                    animation: rotate_close 1s linear;
                }
            }
        }
    }

    &__menu-btn--lines {
        position: absolute;
        background-color: #fff;
        width: 30px;
        height: 1px;
        border-radius: 50px;
        left: 0;
        top: 0;
        bottom: 0;
        margin: auto 0;

        &::before {
            position: absolute;
            content: '';
            background-color: #fff;
            width: 20px;
            height: 1px;
            border-radius: 50px;
            left: 0;
            top: -8px;
        }

        &::after {
            position: absolute;
            content: '';
            background-color: #fff;
            width: 24px;
            height: 1px;
            border-radius: 50px;
            left: 0;
            top: 8px;
        }
    }

    &__close-btn--lines {
        position: absolute;
        border-radius: 50px;
        left: 0;
        top: 0;
        bottom: 0;
        margin: auto 0;

        &::after {
            position: absolute;
            content: '';
            background-color: #fff;
            width: 25px;
            height: 1px;
            border-radius: 50px;
            left: 0;
            top: 0;
            transform: rotate(45deg);
        }

        &::before {
            position: absolute;
            content: '';
            background-color: #fff;
            width: 25px;
            height: 1px;
            border-radius: 50px;
            left: 0;
            top: 0;
            transform: rotate(-45deg);
        }
    }

    @keyframes rotate_close {
        0%,100%{

        }
        30%{
            transform: rotate(0);
        }
        70%{
            transform: rotate(360deg);
        }
    }

    @keyframes menubtn {

        0%{
            width: 30px;
        }

        50% {
            width: 0;
        }

        100% {
            width: 30px;
        }
    }

    @keyframes menubtn1 {
        0% {
            width: 20px;
        }

        50% {
            width: 0;
        }

        100% {
            width: 20px;
        }
    }

    @keyframes menubtn2 {
        0% {
            width: 24px;
        }

        50% {
            width: 0;
        }

        100% {
            width: 24px;
        }
    }

    &__menu {
        display: none;
        background: #121212;
        position: fixed;
        width: 100%;
        height: 100vh;
        z-index: 100;
        animation: menu_anime 0.3s linear;

        &.header__menu--active {
            display: block;
        }

        .container {
            max-width: 54vw;
            height: 100vh;
            position: relative;
        }
    }

    @keyframes menu_anime {
        0% {
            left: -100%;
        }

        100% {
            left: 0;
        }
    }

    &__lines {
        max-width: 1600px;
        margin: 0 auto;
        z-index: -1;
    }

    &__menu-line {
        position: absolute;
        width: 1px;
        height: 100%;
        top: 0;
        left: 50%;
        background: rgba(255, 255, 255, 0.1);
        overflow: hidden;

        &::after {
            content: '';
            display: block;
            position: absolute;
            height: 15vh;
            width: 100%;
            top: -50%;
            left: 0;
            background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, #ffffff 75%, #ffffff 100%);
            animation: drop 7s 0s infinite;
            animation-fill-mode: forwards;
            animation-timing-function: cubic-bezier(0.4, 0.26, 0, 0.97);
        }

        &:nth-child(1) {
            margin-left: -30%;

            &::after {
                animation-delay: 2s;
            }
        }

        &:nth-child(3) {
            margin-left: 30%;

            &::after {
                animation-delay: 2.5s;
            }
        }
    }

    &__menu-bgtext {
        text-transform: uppercase;
        font-size: 100px;
        line-height: 100px;
        font-weight: 700;
        color: #191919;
    }

    &__menu-list {
        margin-top: 20%;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
    }

    &__item+&__item {
        margin-top: 20px;
    }

    &__item {
        padding-left: 100px;
        position: relative;

        &::after {
            position: absolute;
            content: '';
            background-color: #fff;
            left: 0;
            top: 0;
            bottom: 0;
            margin: auto 0;
            width: 6px;
            height: 6px;
            transform: rotate(45deg);
        }

        &:hover {
            &::before {
                position: absolute;
                content: '';
                background: #fff;
                width: 80px;
                height: 1px;
                left: 15px;
                top: 0;
                bottom: 0;
                margin: auto 0;
                animation: links 0.2s linear;
            }
        }
    }

    @keyframes links {
        0% {
            width: 0;
        }

        100% {
            width: 80px;
        }
    }

    &__link {
        color: #fff;
        font-size: 34px;
    }

    &__social-items {
        position: absolute;
        display: flex;
        gap: 15px;
        right: 0;
        bottom: 20px;
    }

    &__social-item {
        font-size: 18px;
        font-weight: 500;
        line-height: 18px;
        letter-spacing: 0.3px;
    }
}

@media (max-width: 1100px){
    .header{
        background-color: #121212;
        &__inner{
            display: flex;
            width: 100%;
            height: 80px;
        }
        &__line{
            transform: translateY(80px);
            width: 100%;
            height: 1px;
            &::after {
                height: 100%;
                width: 10vh;
                top: 0;
                left: -50%;
            }
        }
        @keyframes drop {
            0% {
                left: -50%;
            }
    
            100% {
                left: 110%
            }
        }
    }
}