@import '~normalize.css';
@import './fonts.scss';
@import '../pages/preloader/components/Preloader.module.scss';
@import '../pages//Home/components/Home.module.scss';
@import '../pages/Header/Header.module.scss';

body{
    font-family: 'Roboto Condensed', sans-serif;
    color:#fff;
    background: #121212;
}
h1,h2,h3,h4,h5,h6,p{
    margin: 0; 
    padding: 0;
}
li{
    list-style-type: none;
    text-transform: none;
}
a{
    text-decoration: none !important;
    color: #fff;
}
::selection {
     background: #fff;
     color: #000;
}

.container{
    max-width: 1200px;
    margin: 0 auto;
}

::-webkit-scrollbar{
    width: 0; height: 0;
}